import { Component, OnInit } from '@angular/core';
import {HttpService} from "../../services/http_service/http.service";
import { BACKEND_SERVER_URL } from '../../config/config';

declare var jQuery:any;

@Component({
  selector: 'app-admin-schedules',
  templateUrl: './admin-schedules.component.html',
  styleUrls: ['./admin-schedules.component.css']
})
export class AdminSchedulesComponent implements OnInit {

    private urls: any = {
        retrieve_schedule_url: BACKEND_SERVER_URL + "api/admin/schedule/retrieve_schedule" 
    };
    
    private schedules: any;
    private day_reservations: any;

    constructor( private _httpService: HttpService ) {
        
    }

    ngOnInit() {
       this.showFullCalendar(); 
    }
    
    private getObjectLength(obj){
        var size = 0, key;
        for (key in obj) {
            if (obj.hasOwnProperty(key)) size++;
        }
        return size;
    }
    
    public showFullCalendar() {
        let me = this;
        $('#newyork-full-calendar').fullCalendar({
            header: {
                left: 'prev',
                center: 'title',
                right: 'next'
            },
            editable: false,
            theme: true,
            dayClick: function(date, jsEvent, view) { 
                // Get day of week from date.
                let temp = new Date(date);
                let d = temp.getDay(); 
                
                /*let temp1 = me.schedules[d]; console.log(me.schedules[d]);
                me.day_reservations = temp1;*/
                
                jQuery("#schedule_per_day_modal").modal('show');
            }
        });
        
        this.addEventToCalendar();
    }
    
    public hideModal() { 
        jQuery("#schedule_per_day_modal").modal('hide');
    }
    
    public addEventToCalendar() {
        let me = this;
        
        this._httpService.sendGetRequestWithParams(this.urls.retrieve_schedule_url)
            .subscribe(
                data => {
                    let loginResult = data; me.schedules = loginResult.data;
                    
                    if (loginResult['state'] == 'success') {
                        var date = new Date();
                        var d = date.getDate();
                        var m = date.getMonth();

                        $('#newyork-full-calendar').fullCalendar( 'addEventSource',        
                            function(start, end, callback) {
                                var events = [];

                                for (var loop = start.getTime();loop <= end.getTime();loop = loop + (24 * 60 * 60 * 1000)) {
                                    var test_date = new Date(loop); 
                                    
                                    // Add weekly schedule to fullcalendar.
                                    let day = test_date.getDay(); 
                                    let temp = loginResult['data'][day]; 
                                    let cnt = Object.keys(temp).length; 
                                    if (cnt > 1) {
                                        events.push({
                                            title: cnt + " buses",
                                            start: test_date
                                        });
                                    } else {
                                        events.push({
                                            title: cnt + " bus",
                                            start: test_date
                                        });
                                    }
                                }

                                // return events generated
                                callback( events );
                            }
                        );
                    } 
                },
                error => alert(error),
                () => console.log('Finished')
            );
    }

}



